import React, {useRef} from 'react'
import Container from 'react-bootstrap/Container'

export const SectionWrapper = ({ children, noPadding, className }) => {
  const elem = useRef(null)
  return  (
      <Container
          ref={elem}
          className={`section-wrapper${noPadding ? ' no-padding' : ''}${className ? ` ${className}` : ''}`}
          children={children}
          onScroll={(e) => {
            console.log(elem.current.scrollTop);
              console.log(elem.current.querySelector('.horizontal-scroll'));
          }}
      />
  )
}
